/* PC */
@media (min-width: 415px) {
  .login-background {
    position: fixed;
    height: 100%;
    width: 414px;
    display: flex;
    flex-direction: column;
    padding: 16px;
    align-items: center;
  }
}

/* Mobile */
@media (max-width: 414px) {
  .login-background {
    position: fixed;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 16px;
    align-items: center;
  }
}

.login-back {
  display: flex;
  width: 100%;
  height: 100%;
  padding: 16px;
  align-items: center;
  justify-content: center;
}

.google-btn {
  background-color: #ffffff;
  width: 100%;
  height: auto;
  min-height: 48px;
  border-radius: 8px;
  border: solid 0.5px #e0b0ce;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 12px;
}
